.home {
    margin-bottom: 20px;
    background-color: #fff;
    .c-input {
        display: flex;
        align-items: center;
        border-radius: 25px;
        background: #F8F8F8;
        box-sizing: border-box;
        padding-left: 15px;
        overflow: hidden;
        input {
            flex-shrink: 1;
            width: 500px;
            height: 50px;
            border: none;
            background-color: transparent;
            outline-offset: 0;
            font-size: 16px;
            &::-webkit-input-placeholder {
                font-size: 16px;
                color: #999999;
            }
            &:focus {
                outline-color: #F8F8F8;
                outline-style: hidden;
            }
        }
        button {
            flex-shrink: 0;
            flex-grow: 0;
            width: 70px;
            height: 50px;
            background: #4f7dfa;
            border-radius: 25px;
            background: #4f7dfa url('../../assets/images/icons/search.png') no-repeat center;
            background-size: 24px 24px;
            text-indent: -999rem;
            cursor: pointer;
        }
    }
}

@media screen and (max-width:760px) {
    .home {
        .logo {
            width: 36px;
            height: 36px;
        }
        .c-input {
            input {
                height: 36px;
            }
            button {
                height: 36px;
            }
        }
    }
}